import {
  ActionTypes,
  IAddTeamMemberAction,
  IRemoveTeamMemberAction
} from './types';

export const addTeamMember = (memberId: string): IAddTeamMemberAction => {
  return {
    type: ActionTypes.ADD_TEAM_MEMBER,
    payload: { memberId }
  };
};

export const removeTeamMember = (memberId: string): IRemoveTeamMemberAction => {
  return {
    type: ActionTypes.REMOVE_TEAM_MEMBER,
    payload: { memberId }
  };
};

export type Action = IAddTeamMemberAction | IRemoveTeamMemberAction;
